      assert.equal(true, (0, _utils.isBlank)([]), 'for an empty Array');
      assert.equal(false, (0, _utils.isBlank)({}), 'for an empty Object');
      assert.equal(true, (0, _utils.isBlank)(object), "for an Object that has zero 'length'");
